.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



.question-box{
  display: flex;
  border: 5px solid white;
  text-align: left;
  padding: 50px;
  border-radius: 15px;
  width: 40%;
}

.btn{
  display: block;
  padding: 10px;
  margin: 10px;
  width: 200px;
  border: 1px solid white;
  background-color: transparent;
  color: white;
  font-size:14px;
  border-radius: 10px;
}

.btn:hover, .btn:focus{
  cursor: pointer;
  background-color: #0a0c0f;
  transform: scale(1.05);
  transition-duration: 0.5s;
}

.answer-section{
  display: flex;
  align-items: center;
  flex-direction: column;
  flex: 1;
}

.question-section{
  flex:1;
  flex-direction: column;
}

.result{
  width: 100%;
  align-items: center;
  display: flex;
  flex-direction: column;
}

/* Add responsive styles for smaller screens */
@media (max-width: 650px) {
  .question-box {
    width: 60vw;
    flex-direction: column;
    height: 70%;
  }

  .question-section{
    padding: 0px;
    margin:0px;
    height: 50%;
  }
  .answer-section{
   margin: 0px;
   padding: 0px;
  }
  .btn {
    width: 100%;
  }
}

@media (max-width: 920px) {
  .question-box {
    width: 60vw;
   
    height: 70%;
  }

  .question-section{
    padding: 0px;
    margin:0px;
    height: 100%;
  }
  .answer-section{
   margin: 0px;
   padding: 0px;
   height: 100%;
  }
  .btn {
    width: 100%;
  }
}